import { 
  alpha,
  AppBar,
  Avatar,
  InputBase,
  makeStyles,
  Toolbar,
  Typography,
 } from '@material-ui/core';
import SearchIcon from '@mui/icons-material/Search';
import Badge from '@mui/material/Badge';
import MailIcon from '@mui/icons-material/Mail';
import React from 'react';
import NotificationsIcon from '@mui/icons-material/Notifications';

const useStyles = makeStyles((theme)=>({
  
  logoSm:{
    display:'block',
    [theme.breakpoints.up('sm')]:{
      display:'none'
    }
  },
  logoLg:{
    display:'none',
    [theme.breakpoints.up('sm')]:{
      display:'block',
    }
  },
  searchGroup:{
    display:'flex',
    alignItems:'center',
    width:'50%',
    backgroundColor: alpha(theme.palette.common.white,0.15),
    '&:hover':{
      backgroundColor: alpha(theme.palette.common.white, 0.25)
    },
    [theme.breakpoints.down('sm')]:{
      display:'none'
    }
  },
  inputbase:{
    marginLeft: theme.spacing(1)
  },
  badge:{
    marginRight:theme.spacing(2)
  },
  badgeAndAvatar:{
    display:'flex',
    alignItems:'center'
  },
  toolbar:{
    display:'flex',
    justifyContent:'space-between'
  },
  mobSearchIcon:{
    marginRight:theme.spacing(1),
    display:'block',
    [theme.breakpoints.up('sm')]:{
      display:'none'
    }
  }

}))

function NavBar() {
  const classes = useStyles();
  return (
    <AppBar>
      <Toolbar className={classes.toolbar} >
          {/* Logo Start */}
            <Typography variant='h6' className={classes.logoLg}>
                All India Shipment
            </Typography>
            <Typography variant='h6' className={classes.logoSm}>
                Courier
            </Typography>
          {/* Logo Ends */}

          {/* Search Start */}
          <div className={classes.searchGroup}>
              <SearchIcon/>
              <InputBase placeholder='Search....' className={classes.inputbase} />
          </div>
          {/* Search Ends */}

          {/* Badge Start */}
          <div className={classes.badgeAndAvatar}>
              <div className={classes.mobSearchIcon}>
                <SearchIcon />
              </div>
              <Badge color="secondary" badgeContent={1000} max={99} className={classes.badge} >
                <MailIcon/>
              </Badge>
              <Badge color="secondary" badgeContent={4} className={classes.badge} >
                <NotificationsIcon/>
              </Badge>
              <Avatar alt='G' src="https://avatars.githubusercontent.com/u/17164365?v=4"/>
          </div>
          {/* Badge Ends */}
      </Toolbar>
    </AppBar>
  )
}

export default NavBar


































