const useStyles = makeStyles((theme)=>({
  absolute:{
    position: "fixed",
    bottom:20 ,
    right: 20 ,
  },
  container:{
    width:500,
    height: 550,
    backgroundColor: 'white',
    position:'absolute',
    top: 0 ,
    bottom: 0 ,
    left: 0,
    right:0,
    margin: 'auto',
    [theme.breakpoints.down('sm')]:{
    width: '100vw',
    height: '90vh',
    backgroundColor: 'white',
    position:'absolute',
    top: 0 ,
    bottom: 0 ,
    left: 0,
    right:0,
    margin: 'auto',
    },
  },
  desc:{
    width:'100%'
  },
  title:{
    width:'100%',
  },
  titleDiv:{
    marginBottom: theme.spacing(1)
  },
  descDiv:{
    marginBottom: theme.spacing(1)
  },
  form:{
    marginTop: theme.spacing(3)
    
  },
  dropdown:{
    display:'flex',
    alignItems:'center',
    marginBottom: theme.spacing(1),
    justifyContent:'center'
  },
  whoCanComment:{
    display:"flex",
    justifyContent:'center',
    alignItems:'center',
    marginBottom: theme.spacing(3)
  },
  btns:{
    display:'flex',
    justifyContent:'center',
    alignItems:'center',
  }


}))

function Alart(props){
  return <MuiAlert elevation={6} variant="filled" {...props} />;
};

function Add() {
    const [open,setOpen]= useState(false);
    const [alt , setAlt]= useState(false)
  const classes = useStyles();

  const handleClose = (event,reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setAlt(false);
  };
  return (
    <>
    <Tooltip title="Add">
      <Fab color='primary' className={classes.absolute} onClick={()=>{setOpen(true)}} >
        <AddIcon />
      </Fab>
    </Tooltip>
    <Modal open={open}>
    <Container className={classes.container} >
    <form className={classes.form} autoComplete='off'>
        <div className={classes.titleDiv}>
            <TextField id="outlined-basic" label="Title" variant="outlined" 
            className={classes.title}
            />
        </div>
        <div className={classes.descDiv}>
            <TextField
            id="filled-multiline-static"
            label="Content"
            multiline
            rows={6}
            placeholder="Describe your post"
            variant="filled"
            className={classes.desc}
            />
        </div>
        <div className={classes.dropdown}>
        <FormLabel id="demo-radio-buttons-group-label">Who can see Posts?</FormLabel>
          <TextField select value="Public" >
            <MenuItem value="Public" >Public</MenuItem>
            <MenuItem value="Private" >Private</MenuItem>
            <MenuItem value="Unlisted" >Unlisted</MenuItem>
          </TextField>
        </div>
        <div className={classes.whoCanComment}>
          <FormLabel id="demo-radio-buttons-group-label">Who can Comment?</FormLabel>
          <TextField select value="Public" >
            <MenuItem value="Public" >Public</MenuItem>
            <MenuItem value="Private" >My Friends</MenuItem>
            <MenuItem value="Unlisted" >Nobody</MenuItem>
          </TextField>
        </div>
        <div className={classes.btns}>
          <Button variant="outlined" style={{marginRight:20}} color="primary" onClick={()=>setAlt(true) || setOpen(false) } >Create</Button>
          <Button variant='outlined' color="secondary" onClick={()=> setOpen(false)} >Cancel</Button>
        </div>
    </form>
    </Container>
    </Modal>
    <Snackbar open={alt} anchorOrigin={{vertical:"bottom", horizontal: "left"}} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
          Posted Successfully !!!
        </Alert>
      </Snackbar>
    </>
  )
}

export default Add;